import React from "react";
import withPageLayout from "../hocs/withPageLayout";
import AppInput from "../components/form/AppInput";
import { Grid } from "@material-ui/core";
import AppBtn from "../components/form/AppBtn";
import { withRouter } from "react-router";

const Login = (props) => {
  return (
    <Grid container>
      <Grid item xs={12} md={12}>
        <AppInput label="Username" placeholder={"Enter username"} />
      </Grid>
      <Grid item xs={12} md={12}>
        <AppInput label="Password" placeholder={"Enter password"} password />
      </Grid>
      <AppBtn
        type="contained"
        title="Login"
        block
        loading
        onClick={() => props.history.replace("/onboard/select-house")}
      />
      <AppBtn
        title="Forgot Password"
        block
        type="text"
        onClick={() => props.history.push("/auth/forgot-password")}
      />
    </Grid>
  );
};

export default withRouter(withPageLayout(Login));
